// Footer widgets
@use '../base/' as *;

.simple-social-icons {
    display: flex;
    justify-content: center;
    
    @media screen and (min-width: $large) {
        justify-content: flex-start;
    }

    a {
        padding-left: 0!important;
    }
}

.icon-widget {
    display: flex;
    align-items: baseline;
    padding-top: 20px;
    justify-content: center;
    width: 85%;
    margin: auto;

    @media screen and (min-width: $large) {
        justify-content: flex-start;    
        width: 100%;
        margin: 0;
    }

    i {
        padding-left: 0!important;
    }

    h4 {
        font-size: 18px;
        line-height: 30px;
    }
}


